@use 'base/variables' as v;
@use 'base/mixins' as m;


.tabs{


    @include m.tablet{
        display: flex;
    }

    button {
        background-color: v.$negro;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        transition: background-color 300ms ease-in-out;

        &:hover{
            cursor: pointer;
            background-color: #fae42c;
        }

        &.actual{
            background-color: v.$blanco;
            color: v.$negro;
        }
    }
}